<template>
       <div>
              <el-upload
                      class="excel-uploader"
                      drag
                      :auto-upload= 'false'
                      :action="uploadUrl()"
                      accept=".xls,.xlsx"
                      :multiple= 'false'
                      list-type="picture"
                      :before-upload="beforeUpload"
                      :limit= 1
                      :before-remove="beforeRemove"
                      :on-remove="handleRemove"
                      :on-exceed="handleExceed">
                     <i class="el-icon-upload"></i>
                     <div class="el-upload__text"><em></em></div>
                     <div class="el-upload__tip" slot="tip">excel,10M</div>
              </el-upload>
              <br/>
              <el-button size="small" type="primary" @click="uploadFile"></el-button>
              <el-button size="small" ></el-button>
       </div>
</template>

<script>
       import axios from 'axios';
       import qs from 'qs';
    export default {
           methods: {
                  beforeUpload (file) {
                         console.log('beforeUpload')
                         console.log(file.type)
                         const isText = file.type === 'application/vnd.ms-excel'
                         const isTextComputer = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
                         return (isText | isTextComputer)
                  },
                  handleExceed(files, fileList) {
                         this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `)
                  },
                  beforeRemove(file, fileList) {
                         return this.$confirm(` ${ file.name }`)
                  },
                  handleRemove(file, fileList) {
                         console.log(file, fileList);
                  },
                  uploadUrl:function () {
                         // actionurlapi404action
                         return ""
                  },
                  uploadFile (item) {
                         console.log(item);
                         let form = new FormData();
                         form.append('file',item.file);
                         console.log(form);
                         axios({
                                method:"post",
                                url:"http://localhost:8080/",
                                headers:{'Content-type':'multipart/form-data'},
                                data:form
                         }).then(
                                 res=>{
                                        if(res.data.success){
                                               this.$message({
                                                      type:'info',
                                                      message:''
                                               })
                                        }
                                        else{
                                               this.$message({
                                                      type:'info',
                                                      message:''
                                               })
                                        }

                                 }
                         );
                  }
           }
    }
</script>

